<template>
  <v-col xs="12" sm="12" md="4" lg="3" xl="3">
    <v-hover class="d-flex align-end flex-column">
      <template v-slot:default="{ hover }">
        <v-card min-height="220" min-width="200">
          <v-card-title>{{ dataModel.name[lang] }}</v-card-title>
          <v-spacer></v-spacer>

          <v-row v-show="hover" id="child">
            <v-col>
              <edit-area
                id="dialog"
                :areaData="dataModel"
                v-on:getVal="getVal"
              />
              <delete-area
                transition="slide-x-transition"
                :areaData="dataModel"
                v-on:getVal="getVal"
              />
            </v-col>
          </v-row>
        </v-card>
      </template>
    </v-hover>
  </v-col>
</template>

<script>
export default {
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  methods: {
    getVal() {
      this.$emit("getVal");
    },
  },
  components: {
    "delete-area": () => import("./delete.vue"),
    "edit-area": () => import("./edit.vue"),
  },
  props: ["dataAreas"],
  data() {
    return {
      dataModel: this.dataAreas,
      dialog: false,
    };
  },
};
</script>

<style>
#card-areas {
  display: grid;
  grid-auto-flow: column;
  grid-column-gap: 10px;
}
</style>